.avert-modal-overlay {
  position: relative;
}

.avert-modal-overlay::after {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.avert-modal-link {
  display: inline-block;
  margin-top: -1rem;
  width: 1rem;
  height: 1rem;
  background-image: url('~app/components/App/icons.svg');
  background-size: 400px 200px;
  background-position: -10px -10px;

  position: relative;
  left: 1px;
  bottom: -3px;
  -webkit-transform: scale(0.75);
      -ms-transform: scale(0.75);
          transform: scale(0.75);

  /* hide text */
  text-indent: 400%;
  white-space: nowrap;
  overflow: hidden;
}

.avert-modal {
  position: fixed;
  z-index: 2;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  width: calc(100% - 3rem);
  max-width: 30rem;
  max-height: calc(100vh - 1rem);
  padding: 1.5rem 1.75rem;
  font-size: 0.875rem !important;
  -webkit-box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.33);
          box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.33);
  background-color: white;

  -webkit-animation-timing-function: ease-out;

          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.avert-modal[data-active='true'] {
  /*animation-name: scale-up;*/
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
}

.avert-modal[data-active='false'] {
  display: none;
}

.avert-modal[data-closing='true'] {
  /*display: inherit;*/
  /*animation-name: scale-down;*/
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}

.avert-modal p {
  font-size: 0.875rem;
}

.avert-modal .avert-modal-close {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  padding: 0 0.5rem;
  font-weight: bold;
  font-size: 1.25rem;
  color: white;
  background-color: rgba(0, 0, 0, 0.66);
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  text-decoration: none;
  opacity: 0.25;
}

.avert-modal .avert-modal-close:hover,
.avert-modal .avert-modal-close:focus {
  opacity: 0.75;
}

@-webkit-keyframes scale-up {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0);
            transform: translateX(-50%) translateY(-50%) scale(0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
  }
}

@keyframes scale-up {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0);
            transform: translateX(-50%) translateY(-50%) scale(0);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
  }
}

@-webkit-keyframes scale-down {
  from {
    opacity: 1;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0);
            transform: translateX(-50%) translateY(-50%) scale(0);
  }
}

@keyframes scale-down {
  from {
    opacity: 1;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(1);
            transform: translateX(-50%) translateY(-50%) scale(1);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-50%) scale(0);
            transform: translateX(-50%) translateY(-50%) scale(0);
  }
}

/* --- media queries --- */
@media (min-width: 25em) {
  .avert-modal-link {
    -webkit-transform: scale(0.8125);
        -ms-transform: scale(0.8125);
            transform: scale(0.8125);
  }
}

@media (min-width: 30em) {
  .avert-modal-link {
    -webkit-transform: scale(0.875);
        -ms-transform: scale(0.875);
            transform: scale(0.875);
  }
}

@media (min-width: 35em) {
  .avert-modal-link {
    -webkit-transform: scale(0.9375);
        -ms-transform: scale(0.9375);
            transform: scale(0.9375);
  }
}

@media (min-width: 40em) {
  .avert-modal-link {
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
  }
}



