.turbine-base {
  fill: #0092cc;
}
.turbine-blades path {
  fill: #00bee6;
}
.turbine-blades circle {
  fill: none;
}
.turbine-cap {
  fill: #03d5e5;
}

.turbine-blades {
  -webkit-transform-origin: 88px 86.78px;
      -ms-transform-origin: 88px 86.78px;
          transform-origin: 88px 86.78px; /* center of blades */
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



